<style scoped>
* {
  user-select: none;
}
</style>
<template>
  <div>
    <div style="max-width: 900px" class="mx-auto mt-2 pb-6">
      <div class="d-flex align-center px-8">
        <v-text-field
          label="Число слов для генерации"
          placeholder="Введите число"
          v-model="text_count"
          type="number"
          :min="2"
          color="gray"
        ></v-text-field>
        <v-btn
          class="ms-4"
          @click="
            generate();
            trueStart();
            view_result = false;
            answer = false;
          "
          >Старт</v-btn
        >
      </div>
      <div>
        <div class="d-flex align-center justify-space-between px-8 mb-4">
          <div>
            <v-btn
              @click="text_count = 5"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >5</v-btn
            >
            <v-btn
              @click="text_count = 10"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >10</v-btn
            >
            <v-btn
              @click="text_count = 15"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >15</v-btn
            >
            <v-btn
              @click="text_count = 20"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >20</v-btn
            >
            <v-btn
              @click="text_count = 25"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >25</v-btn
            >
            <v-btn
              @click="text_count = 30"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >30</v-btn
            >
            <v-btn
              @click="text_count = 50"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >50</v-btn
            >
          </div>
          <div>
            <stop-watch :running="start" :resetWhenStart="true" />
          </div>
        </div>
      </div>
      <div v-if="!answer" style="max-width: 275px" class="mx-auto mt-2 px-4">
        <div
          class="px-2 py-1 mb-1 pritty-border"
          v-for="(item, index) in text_view"
          :key="index"
        >
          {{ index + 1 }}. {{ item }}
        </div>
      </div>
      <div
        v-if="!start && answer && !view_result"
        style="max-width: 275px"
        class="mx-auto mt-2 px-4"
      >
        <div
          class="px-2 py-1 mb-1 d-flex pritty-border"
          v-for="(item, index) in text_view_answer"
          :key="index"
        >
          {{ index + 1 }}.&ensp;<input
            type="text"
            v-model="text_view_answer[index].answer"
            class="pritty-input"
          />
        </div>
      </div>
      <div
        v-if="view_result"
        style="max-width: 275px"
        class="mx-auto mt-2 px-4 mb-8"
      >
        <div
          class="px-2 py-1 mb-1 pritty-border"
          v-for="(item, index) in text_view_answer"
          :key="index"
        >
          <div>{{ index + 1 }}. {{ item.value }}</div>
          <div :style="validateAnswer(item) ? 'color:green' : 'color:red'">
            {{ index + 1 }}. {{ item.answer }}
          </div>
        </div>
      </div>

      <div v-if="start" style="max-width: 275px" class="mx-auto mt-2 px-4 mb-8">
        <v-btn
          color="primary"
          :disabled="!start"
          block
          @click="
            start = false;
            answer = true;
          "
          >Ответить</v-btn
        >
      </div>

      <div
        v-if="!view_result && !start && answer"
        style="max-width: 275px"
        class="mx-auto mt-2 px-4 mb-8"
      >
        <v-btn color="primary" block @click="view_result = true"
          >Ответить</v-btn
        >
      </div>
    </div>
  </div>
</template>
<style scoped>
.pritty-border {
  border: 2px solid #ddd;
}
.theme--dark .pritty-border {
  border: 2px solid #4e4e4e;
}
.pritty-input{
  width: 100%; 
  outline: 0; 
  border-bottom: 1px solid #222;
}
.theme--dark .pritty-input {
  border-bottom: 1px solid #4e4e4e;
  color: #fff;
}
</style>
<script>
import StopWatch from "./StopWatch.vue";
export default {
  name: "TextBlock",
  components: {
    StopWatch,
  },
  data: () => ({
    text_count: 20,
    text: require("!raw-loader!@/assets/text.txt").default.split("\n"),
    text_view: [],
    text_view_answer: [],
    start: false,
    view_result: false,
    answer:false,
  }),
  methods: {
    validateAnswer(item) {
      if (item.value.toLowerCase().trim() == item.answer.toLowerCase().trim())
        return true;
      return false;
    },
    trueStart() {
      this.start = false;
      this.$nextTick(() => {
        this.start = true;
      });
    },
    generate() {
      let randomWords = [];
      let currentIndex = this.text.length;
      let temporaryValue, randomIndex;

      while (0 !== currentIndex) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        temporaryValue = this.text[currentIndex];
        this.text[currentIndex] = this.text[randomIndex];
        this.text[randomIndex] = temporaryValue;
      }

      for (let i = 0; i < this.text_count; i++) {
        randomWords.push(this.text[i]);
      }
      this.text_view = randomWords;
      this.text_view_answer = randomWords.map((item) => ({
        value: item,
        answer: "",
      }));
    },
  },
  mounted() {
    if ("URLSearchParams" in window) {
      var searchParams = new URLSearchParams(window.location.search);
      searchParams.set("category", "word");
      var newRelativePathQuery =
        window.location.pathname + "?" + searchParams.toString();
      history.pushState(null, "", newRelativePathQuery);
    }
  },
};
</script>
