  methods: {
    build() {
      this.cardMax = this.cardMaxTemp;
      let shuffleArray = function (array) {
        for (let i = array.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [array[i], array[j]] = [array[j], array[i]];
        }
        return array;
      };
      this.cardsView = shuffleArray(this.cards);
      this.cardIndex = 1;
      this.$nextTick(() => {
        this.cardIndex = 0;
      });
      this.start = false;
      this.answer = false;
      this.answer_view = false;
      this.answer_text = "";
    },
    verify() {
      const abbreviationArray = this.cardsView
        .slice(0, this.cardMax)
        .map((card) => card.abbreviation);
      let answer = this.answer_text.trim().split("\n");
      answer =answer.map(function(item) {
        let answer_item = (item.trim()).toUpperCase()
        return answer_item==""?"x":answer_item;
      });
      
      if (answer.length > abbreviationArray.length && this.answer_count) {
        alert(
          "Количесво карт больше допустимого. Возможно: " +
            abbreviationArray.length +
            ". Введено: " +
            answer.length
        );
      } else if (answer.length < abbreviationArray.length && this.answer_count) {
        alert(
          "Количесво карт меньше допустимого. Возможно: " +
            abbreviationArray.length +
            ". Введено: " +
            answer.length
        );
      } else {
        this.answer_view = true;
      }

      while (answer.length < abbreviationArray.length) {
        answer.push("x");
      }

      while (answer.length>abbreviationArray.length) {
        abbreviationArray.push(["X","X"]);
      }
      console.log(abbreviationArray)
      if(answer.length>0){
        if (answer[0]=="")answer[0]="x"
      }
      this.answer_user = answer;
      this.answer_true = abbreviationArray;
    },
  },
  computed:{
    smile(){
      if(this.cardMax>46){
        return '))))'

      }else if(this.cardMax>40){
        return ')))'
      }else if(this.cardMax>34){
        return '))'
      }else if(this.cardMax>28){
        return ')'
      }else if(this.cardMax>22){
        return '('
      }else if(this.cardMax>16){
        return '(('
      }else if(this.cardMax>10){
        return '((('
      }
      return '(((('
    }
