import Vue from 'vue';
import Vuetify from 'vuetify/lib/framework';

Vue.use(Vuetify);
const darkThemeKey = 'theme-dark'; // Ключ для Local Storage

const savedTheme = localStorage.getItem(darkThemeKey);
const isDarkTheme = savedTheme !== null ? savedTheme === 'true' : false;

export default new Vuetify({
    theme: {
        dark:isDarkTheme,
        themes: {
            light: {
              primary: '#1976D2',
            },
            dark: {
              primary: '#ebb757',
            },
          },
    }
});
