<style scoped>
* {
  user-select: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
</style>
<template>
  <div>
    <div style="max-width: 900px" class="mx-auto mt-2 pb-6">
      <div class="d-flex align-center px-8">
        <v-text-field
          label="Число цифр для генерации"
          placeholder="Введите число"
          v-model="number_count"
          type="number"
          :min="2"
          color="gray"
        ></v-text-field>
        <v-btn
          class="ms-4"
          @click="
            generate();
            trueStart();
            view_result = false;
            answer = false;
          "
          >Старт</v-btn
        >
      </div>
      <div>
        <div class="d-flex align-center justify-space-between px-8 mb-4">
          <div>
            <v-btn
              @click="number_count = 10"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >10</v-btn
            >
            <v-btn
              @click="number_count = 15"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >15</v-btn
            >
            <v-btn
              @click="number_count = 20"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >20</v-btn
            >
            <v-btn
              @click="number_count = 25"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >25</v-btn
            >
            <v-btn
              @click="number_count = 30"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >30</v-btn
            >
            <v-btn
              @click="number_count = 50"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >50</v-btn
            >
            <v-btn
              @click="number_count = 70"
              height="25"
              min-width="50"
              max-width="50"
              class="me-1 mb-1"
              >70</v-btn>
          </div>
          <div>
            <stop-watch :running="start" :resetWhenStart="true" />
          </div>
        </div>
      </div>
      <div v-if="!answer" style="max-width: 275px;flex-wrap: wrap" class="mt-2 mx-auto px-4 d-flex">
        <div
          class="mb-1" style="padding-right:1px"
          v-for="(item, index) in number_view"
          :key="index"
        >
            <div class="px-2 py-1 pritty-border" 
                :style="(index+1)%6==0?{borderRight:'2px solid var(--primary)'}:{}">
                {{ item }}
            </div>
        </div>
      </div>
      <div
        v-if="!start && answer && !view_result"
        style="max-width: 275px;flex-wrap: wrap"
        class="mt-2 mx-auto px-4 d-flex">
        <div
          v-for="(item, index) in number_view_answer"
          :key="index"
          class="mb-1" style="padding-right:1px"
        >
            <input 
                @input="handleNumberInput(index)" 
                :id="'num'+index"
                class="px-2 py-1 mx-0 d-flex pritty-border pritty-input"
                style="width:29px"
                type="number"
                v-model="number_view_answer[index].answer"
                
            />
        </div>
      </div>
      <div
        v-if="view_result"
        style="max-width: 275px"
        class="mx-auto mt-2 px-4 mb-8"
      >
        <div
          class="px-2 py-1 mb-1 pritty-border"
          v-for="(item, index) in number_view_answer"
          :key="index"
        >
          <div>{{ index + 1 }}. {{ item.value }}</div>
          <div :style="validateAnswer(item) ? 'color:green' : 'color:red'">
            {{ index + 1 }}. {{ item.answer }}
          </div>
        </div>
      </div>

      <div v-if="start" style="max-width: 275px" class="mx-auto mt-2 px-4 mb-8">
        <v-btn
          color="primary"
          :disabled="!start"
          block
          @click="
            start = false;
            answer = true;
          "
          >Ответить</v-btn
        >
      </div>

      <div
        v-if="!view_result && !start && answer"
        style="max-width: 275px"
        class="mx-auto mt-2 px-4 mb-8"
      >
        <v-btn color="primary" block @click="view_result = true"
          >Ответить</v-btn
        >
      </div>
    </div>
  </div>
</template>
<style scoped>

.pritty-border {
  border: 2px solid #ddd;
}
.theme--dark .pritty-border {
  border: 2px solid #4e4e4e;
}
.pritty-input{
}
.theme--dark .pritty-input {
  color: #fff;
}
</style>
<script>
import StopWatch from "./StopWatch.vue";
export default {
  name: "TextBlock",
  components: {
    StopWatch,
  },
  data: () => ({
    number_count: 30,
    number_view: [],
    number_view_answer: [],
    start: false,
    view_result: false,
    answer:false,
  }),
  methods: {
    validateAnswer(item) {
      if (item.value+''.toLowerCase().trim() == item.answer+''.toLowerCase().trim())
        return true;
      return false;
    },
    trueStart() {
      this.start = false;
      this.$nextTick(() => {
        this.start = true;
      });
    },
    handleNumberInput(index) {
        let str = (this.number_view_answer[index].answer+'')
        if(index==this.number_view_answer.length-1 && str.length>0){
            this.number_view_answer[index].answer = this.number_view_answer[index].answer = +str[0]
            let inputElement = document.getElementById("num"+index);
            // Убираем фокус с элемента
            inputElement.blur();
            return
        }
        if(str.length>1){
            for (let i = 1; i < str.length; i++) {
                this.number_view_answer[index+i].answer = +str[i]
                let inputElement = document.getElementById("num"+(index+i));
                inputElement.focus();
            }
            this.number_view_answer[index].answer = +str[0]
        }
        //this.number_view_answer[index+1].answer = this.number_view_answer[index].answer
    },
    generate() {
        const n = this.number_count;
        let number_view = []
        for (let i = 0; i < n; i++) {
            const randomDigit = Math.floor(Math.random() * 10); // Генерация случайной цифры от 0 до 9
            number_view.push(randomDigit);
        }
        this.number_view = number_view
        this.number_view_answer = number_view.map((item) => ({
            value: item,
            answer: "",
        }));
    },
  },
  mounted() {
    if ("URLSearchParams" in window) {
      var searchParams = new URLSearchParams(window.location.search);
      searchParams.set("category", "number");
      var newRelativePathQuery =
        window.location.pathname + "?" + searchParams.toString();
      history.pushState(null, "", newRelativePathQuery);
    }
  },
};
</script>
