<template>
  <v-app>
    
    <div class="py-1 px-2 elevation-2 text-center d-flex justify-space-between align-center">
      <v-app-bar-nav-icon
        @click="drawer=true"
      ></v-app-bar-nav-icon>
      <h1>MNEMO;)</h1>
      <span>
        <v-img style="cursor:pointer" @click="toggleTheme()" max-width="35" max-height="35" :src="require('@/assets/day-and-night-min.png')"></v-img>
      </span>
    </div>

    <v-navigation-drawer
      v-model="drawer"
      absolute
      left
      temporary
    >
      <v-list
        nav
        dense
      >
        <v-list-item-group
        >
          <v-list-item @click="memory=0;drawer=false">
            <v-list-item-title>Карты</v-list-item-title>
          </v-list-item>

          <v-list-item @click="memory=1;drawer=false">
            <v-list-item-title>Слова</v-list-item-title>
          </v-list-item>
          <v-list-item @click="memory=2;drawer=false">
            <v-list-item-title>Числа</v-list-item-title>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <Card v-if="memory==0"/>
    <TextBlock v-else-if="memory==1"/>
    <NumberBlock v-else-if="memory==2"/>
  </v-app>
</template>

<style>
:root{
  --primary: #1976D2;
  --disable: #e0e0e0;
}
:root .theme--dark{
  --primary: #ebb757;
  --disable: #3d3d3d;
}


.theme--dark.v-application{
  background: #222;
}
.theme--dark.v-navigation-drawer{
  background: #222;  
}
</style>
<script>
import Card from './components/CardBlock';
import TextBlock from './components/TextBlock';
import NumberBlock from './components/NumberBlock';
export default {
  name: 'App',

  components: {
    Card,
    TextBlock,
    NumberBlock,
  },
  methods: {
      toggleTheme() {
        this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
        localStorage.setItem('theme-dark', this.$vuetify.theme.dark); // Сохраняем состояние темы
      },
    },
  data: () => ({
    drawer:false,
    memory:-1,
  }),
  mounted(){
    const urlParams = new URLSearchParams(window.location.search);
    const category = urlParams.get('category');
    if(category=="word"){
      this.memory=1
    }else if(category=="number"){
      this.memory=2
    }else{
      this.memory=0
    }
  }
};
</script>
